import React from 'react';
import s from './ProfileInfo.module.css';
import { reduxForm } from 'redux-form';
import { createField, InputArea } from '../../common/FormsControls/FormsControls';

const input = InputArea("input");

const ProfileDataForm = ({handleSubmit, initialValues, offEditMode}) => {
    return (
    <form className={s.descriptionRightColunn} onSubmit={handleSubmit} onBlur={offEditMode}>
        <div className><button className={s.editDescriptionButton}>save</button></div>
        <div className={s.descriptionItem}>
            <b>Fullname: </b> { createField('Full name', 'fullName', [], input, {autoFocus: true}) }
        </div>
        <div className={s.descriptionItem}><b>Contacts:</b></div>
        <div className={s.contactsBlock}>{
            Object.entries(initialValues.contacts).map(([k, v]) => v ? <div><b>{k}</b>: <a target="_blank" href={v}>{v}</a></div> : null)
        }</div>
        <div className={s.descriptionItem}>
            <b>Looking for a job:</b>
            { createField("", "lookingForAJob", [], input, {type: "checkbox"})}
        </div>
        <div className={s.descriptionItem}>
            <b>My professionals skills:</b>
            { createField('My professionals skills', 'lookingForAJobDescription', [], input) }
        </div>
        <div className={s.descriptionItem}><b>About me:</b></div>
        <div className={s.descriptionItem}>
            { createField('About me', 'aboutMe', [], input) }
        </div>
    </form>
    )
}

const ProfileDataReduxForm = reduxForm({ form: 'editProfile' })(ProfileDataForm)

export default ProfileDataReduxForm